.icon {
  @include textBody;
  display: grid;
  place-items: center;
  width: 1.5rem;
  height: 1.5rem;
  color: $accent-orange;
  transition: color $transition-time;

  &:hover,
  &:active {
    color: $white;
  }

  &--header-icon {
    color: $black;

    &:hover {
      color: $accent-orange;
    }

    &:active {
      color: $dark-orange;
    }
  }

  &--active {
    margin-right: 15px;
  }
}
